import { defineStore } from 'pinia';
import { getCategories, getServices } from '../services/api';

export const useServicesStore = defineStore('services', {
  state: () => ({
    categories: [],
    featuredServices: [],
    loading: false,
    error: null,
  }),

  actions: {
    async fetchCategories() {
      this.loading = true;
      this.error = null;
      try {
        const response = await getCategories();
        this.categories = response;
        return response;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async fetchFeaturedServices() {
      this.loading = true;
      this.error = null;
      try {
        const response = await getServices();
        // For now, just take the first 6 services as featured
        this.featuredServices = response.slice(0, 6);
        return this.featuredServices;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },
  },
}); 